								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+respic[0]" alt="" />
								<big-img v-if="showres" @clickit="viewres" :imgSrc="resPicsrc"></big-img>
								<span @click="showipt" v-show='rautionoks' class="resp" :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span>
								<i class="el-icon-view" @click='clickres'></i>
							</div>
							<img-upload @send-image='getRespic' :ImageSize='imgSize'></img-upload>
							<div class="text-warp">
								<p class="text top pstion">JPGJPEGPNG2M</p>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="">
						<div class="picInfo">
							<div class="confim left" v-if="confm.length==0">
								<img src="../../img/confim.png" alt="" />
							</div>
							<div class="confim left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+confm[0]" alt="" />
								<big-img v-if="showcos" @clickit="viewcos" :imgSrc="cosPicsrc"></big-img>
								<span @click="showipt" v-show='rautionoks' class="resp" :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span>
								<i class="el-icon-view" @click='clickcos'></i>
							</div>
							<img-upload @send-image='getConfimpic' :ImageSize='imgSize'></img-upload>
							<div class="downLoad">
								<p class="load"></p>
								<p class="text">JPGJPEGPNG2M</p>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model='forms.business_scope' v-show='!rautionoks'></el-input>
						<span class="domain" v-show='rautionoks'>{{shopsbusiness.business_scope}}</span>
					</el-form-item>
					<div class="bottom">
						<button type='button' class="blue" @click='onSubmit'></button>
						<button type="button" class="default" @click='goBack'></button>
					</div>
				</el-form>
			</div>
		</section>
	</div>
</template>

<script>
	import { saveShopinfo,CommerStatus } from '../../../api';
	import { getShopinfo } from '../../../api';
	import { Util } from '@/config/util';
	import { Business,Idcode,Dining  } from '@/config/cfg';
	import ImgUpload from '@/components/upload/SUpload';
	import { Confimcard , Character,Busnumber,Resnumber } from '../../../index';
	import BigImg from '../../../components/showBigImg';
	import { Public } from '@/config/public';
	export default {
		components:{
			ImgUpload,
			BigImg
		},
		data() {
			return {
				options: [{   //
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					},
					{
						value: ''
					},
					{
						value: ''
					}, 

					{	value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}, {
						value: ''
					}
				],
				value: '', //
				date1: '', //
				date2: '', //
				value5: '', //
				forms: {
					company_name: '',   //
					legal_person: '',   //
					legal_card: '',   //
					business_num: '',//  
					repast_permit_identity: '', //
					repast_permit_year: '', //
					repast_permit_num: '', // 
					business_scope: '' //
				},
				shopsbusiness: {},   //
				dates: [],  //
				years: '', // 
				imgbase_url: imgbase_url, //
				show: true, //
				shows:true, //
				dat: [],  //
				cert:{},  //
				bscert:'', //
				idcert:'', //
				rscert:'', //
				inaution:'or', //class
				autionerr:'reds', //class
				autionok:'gr', //class
/**/
				rinautions:false,
				rautionoks:false,
				rautionerrs:false,
				binautions:false,
				bautionoks:false,
				bautionerrs:false,
				Iinautions:false,
				Iautionoks:false,
				Iautionerrs:false,
/**/
				legal_card_photo:[],//
				imgbase_url: imgbase_url , //
				buspic:[], //
				respic:[], //
				confm:[], //
				imgSize:2048, //
				imgSrc:'',
				showImg:false,
				showimg:false,
				showbus:false,
				showres:false,
				showcos:false,
				Imgsrc:'',
				busPicsrc:'',
				resPicsrc:'',
				cosPicsrc:''
			};
		},
		created() {
			this.getData();
			this.getStatus();
		},
		computed:{
			Business(){
				return Business;
			},
			Idcode(){
				return Idcode;
			},
			Dining(){
				return Dining;
			}
		},
		methods: {
	        getLegarcard(res){     	
	        	this.legal_card_photo.push(res);
	        	if(this.legal_card_photo.length>2){
	        		this.legal_card_photo = [];
	        	}
	        },
	        getBuspic(res){
	        	this.buspic = [];
	        	this.buspic.push(res);
	        },
	        getRespic(res){
	        	this.respic = [];
	        	this.respic.push(res);
	        },
	        getConfimpic(res){
	        	this.confm = [];
	        	this.confm.push(res);
	        },
	         clickImg() {
                this.showImg = true;
                // 
                this.imgSrc = this.legal_card_photo[0];
           },
            viewImg(){
                this.showImg = false;
            },
	        clickimg(){
	        	 this.showimg = true;
                // 
                this.Imgsrc = this.legal_card_photo[1];
	        },
	        viewimg(){
	        	this.showimg = false;
	        },
	        clickbus(){
	        	this.showbus = true;
	        	this.busPicsrc = this.buspic[0];
	        },
	        viewbus(){
	        	this.showbus = false;
	        },
	         clickres(){
	        	this.showres = true;
	        	this.resPicsrc = this.respic[0];
	        },
	        viewres(){
	        	this.showres = false;
	        },
	         clickcos(){
	        	this.showcos = true;
	        	this.cosPicsrc = this.confm[0];
	        },
	        viewcos(){
	        	this.showcos = false;
	        },
			
/**/
			onSubmit() {
					if(this.isEmpty(this.forms)){
						alert('')
						return ;
					}
					if(!Character(this.forms.company_name)){
						alert('!!')
						return ;
					}
					if(this.forms.company_name==''){
						alert('')
						return;
					}
					if(this.forms.legal_person==''){
						alert('')
						return;
					}
					if(this.forms.legal_card==''){
						alert('')
						return;
					}
					
					if(!Confimcard(this.forms.legal_card)){
						alert('')
						return ;
					}
					if(!Character(this.forms.business_scope)){
						alert('')
						return ;
					}
					if(!Busnumber(this.forms.business_num)){
						alert('')
						return ;
					}
					if(!Resnumber(this.forms.repast_permit_num)){
						alert('12');
						return ;
					}
				
				let t = new Date(this.date1);
				let startTime = t.getFullYear() + '-' + (t.getMonth() + 1) + '-' + t.getDate();
				let statime = startTime;
				let sta1 = Date.parse(new Date(statime));
				sta1 = sta1 / 1000;
				let t2 = new Date(this.date2);
				let endTime = t2.getFullYear() + '-' + (t2.getMonth() + 1) + '-' + t2.getDate();
				let entime = endTime;
				let sta2 = Date.parse(new Date(entime));
				sta2 = sta2 / 1000;
				this.dates.push(sta1, sta2);
				let t3 = new Date(this.value5);
				this.years = t3.getFullYear();
				let shop_id = Public.getShopid();
				let data = {
					save_shop_business: 1,
					shop_id:shop_id,
					company_name: this.forms.company_name,
					legal_person: this.forms.legal_person,
					legal_card: this.forms.legal_card,
					business_num: this.forms.business_num,
					repast_permit_identity: this.value,
					repast_permit_year: this.years,
					legal_card_photo:this.legal_card_photo,
					business_photo:this.buspic[0],
					repast_permit_photo:this.respic[0],
					confirmation:this.confm[0],
					repast_permit_num: this.forms.repast_permit_num,
					business_scope: this.forms.business_scope,
					business_date: this.dates
				};
				saveShopinfo(data, this.alts);
			},
				alts(resp) {
					if(resp.ret === 0) {
						
						this.$router.go(-1);
						
					} else {
						alert('');
					}
			},
/**/
			goBack() {   //
				this.$router.go(-1);
			},
			
			isEmpty(obj){
				for(let key in obj){
					if(!obj[key]){
						return;
					}
				}
			},
			
			showipt(){
				
				this.bautionerrs = false;
				this.show = false;
				this.shows = false;
			},
			
/**/
			getData() {
				let _this = this;
				getShopinfo({
					get_shop_business: 1,
				}, function(resp) {
					if(resp){
						_this.shopsbusiness = resp.data.shopsbusiness||{};
						_this.forms.company_name = _this.shopsbusiness.company_name;
						_this.forms.legal_person = _this.shopsbusiness.legal_person;
						_this.forms.legal_phone = _this.shopsbusiness.legal_phone;
						_this.forms.legal_card = _this.shopsbusiness.legal_card;
						_this.forms.business_num = _this.shopsbusiness.business_num;
						_this.forms.business_scope = _this.shopsbusiness.business_scope;
						_this.value5 = new Date(String(_this.shopsbusiness.repast_permit_year));
						_this.value = _this.shopsbusiness.repast_permit_identity;
						_this.forms.repast_permit_num = _this.shopsbusiness.repast_permit_num;
						_this.legal_card_photo = _this.shopsbusiness.legal_card_photo || [];
						if(!Util.isEmpty(_this.shopsbusiness.business_date)){
							let time1 = _this.shopsbusiness.business_date.slice(1, length - 1);
							let arr = time1.split(',');
							let time2 = arr[0];
							let stime = new Date();
							stime.setTime(time2 * 1000);
							_this.date1 = stime.toString();
							let time3 = arr[1];
							let etime = new Date();
							etime.setTime(time3 * 1000);
							_this.date2 = etime.toString();
							let time4 = _this.shopsbusiness.business_date.slice(1, length - 1);
							let arr3 = time1.split(',');
							let time5 = arr[0];
							let time6 = arr[1];
							let s1time = Util.TimeTo(time5, 'yyyy-MM-dd');
							let e1time = Util.TimeTo(time6, 'yyyy-MM-dd');
							_this.dat.push(s1time, e1time);
						}
						if(!Util.isEmpty(_this.shopsbusiness.business_photo)){
							_this.buspic.push(_this.shopsbusiness.business_photo);
						}
						if(!Util.isEmpty(_this.shopsbusiness.repast_permit_photo)){
							_this.respic.push(_this.shopsbusiness.repast_permit_photo);
						}
						if(!Util.isEmpty(_this.shopsbusiness.confirmation)){
							_this.confm.push(_this.shopsbusiness.confirmation);
						}
						
					}
				});
			},
/**/
			getStatus(){
				let _this =this;
				CommerStatus({
					get_shop_bs_status: 1,
				},function(resp){
					if(resp){
						_this.cert = resp.data.shop_bs_status||{};
					if(_this.cert.bs_code===Business.AUTONERR){
						_this.bscert = Business.toString(Business.AUTONERR);
						_this.bautionerrs  = true;
					}else if(_this.cert.bs_code===Business.NO){
						_this.bscert = Business.toString(Business.NO);
					}else if(_this.cert.bs_code===Business.INAUTON){
						_this.bscert = Business.toString(Business.INAUTON);
					}else if(_this.cert.bs_code===Business.YES){
						_this.bscert = Business.toString(Business.YES);
					}
					if(_this.cert.id_code===Idcode.AUTONERR){
						_this.idcert = Idcode.toString(Idcode.AUTONERR);
					}else if(_this.cert.id_code===Idcode.NO){
						_this.idcert = Idcode.toString(Idcode.NO);
						_this.Iautionerrs = true;
//						_this.forms.company_name = '';
//						_this.forms.legal_person = '';
//						_this.forms.legal_card = '';
					}else if(_this.cert.id_code===Idcode.INAUTON){
						_this.idcert = Idcode.toString(Idcode.INAUTON);
						_this.Iinautions = true;
					}else if(_this.cert.id_code===Idcode.YES){
						_this.idcert = Idcode.toString(Idcode.YES);
					}
					if(_this.cert.rs_code===Dining.AUTONERR){
						_this.rscert = Dining.toString(Dining.AUTONERR);
					}else if(_this.cert.rs_code===Dining.NO){
						_this.rscert = Dining.toString(Dining.NO);
					}else if(_this.cert.rs_code===Dining.INAUTON){
						_this.rscert = Dining.toString(Dining.INAUTON);
					}else if(_this.cert.rs_code===Dining.YES){
						_this.rscert = Dining.toString(Dining.YES);
						_this.rautionoks = true ;
					}
					}
					
				});
			}
		}
	};
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		.domain {
			padding-left: 14px;
		}
		.nvs {
			background: #EFF3FA;
			height: 26px;
		}
		button {
			cursor: pointer;
		}
		.text-warp {
